import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: () => import('../views/layout/MainLayout.vue'),
    redirect: '/cuslazyCode',
    children: [
      {
        path: '/cuslazyCode',
        name: 'cuslazyCode',
        component: () => import('../views/cuslazyCode/CusLazyCode.vue')

      },
      {
        path: '/cusLazyCodeDetailed',
        name: 'cusLazyCodeDetailed',
        component: () => import('../views/cuslazyCode/CusLazyCodeDetailed.vue')

      },
      {
        path: '/clipImg',
        name: 'clipImg',
        component: () => import('../views/clipImg/ClipImg.vue')

      }
    ]
  },
  {
    path: '*', // 匹配任何路径
    redirect: '/ErrorPage'

  },
  {
    path: '/ErrorPage',
    name: 'ErrorPage',
    component: () => import('../views/error/ErrorPage.vue')

  }
]

const router = new VueRouter({
  // mode: 'history',
  routes
})

export default router
